html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 0.69em;
  height: 1em;
  fill: #FFF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

@font-face {
  font-family: "bebas-font";
  src: url("../fonts/bebas-webfont.woff2") format("woff2"), url("../fonts/bebas-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "helvetic-font";
  src: url("../fonts/helveticaneue.woff2") format("woff2"), url("../fonts/helveticaneue.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  background-color: #ffffff;
  background-image: url(../img/pattern-bg.png);
  background-size: 50px;
  background-position: 50%;
  font-family: helvetic-font,sans-serif;
  font-size: 12px;
  font-weight: 1.5;
  color: #414141;
  font-weight: 400;
}

@media only screen and (max-width: 959px) {
  body {
    background-image: none;
  }
}

cite {
  font-style: normal;
}

img {
  max-width: 100%;
}

.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 0 10px;
}

@media only screen and (max-width: 959px) {
  .container {
    margin: 0 5px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-title {
  position: relative;
  background-color: #e8603c;
}

.main-title:before, .main-title:after {
  content: "";
  position: absolute;
  top: 0;
  width: 1000px;
  height: 100%;
  background: #e8603c;
  opacity: 0.5;
}

@media only screen and (max-width: 959px) {
  .main-title:before, .main-title:after {
    display: none;
  }
}

.main-title:before {
  right: 100%;
  margin-right: 10px;
}

.main-title:after {
  left: 100%;
  margin-left: 10px;
}

.main-title__text {
  padding: 14px 5px 14px 18px;
  font-family: bebas-font,sans-serif;
  line-height: 1.33;
  font-size: 24px;
  font-weight: 500;
  text-transform: uppercase;
  color: #ffffff;
}

.header-page {
  overflow: hidden;
}

.header-page__wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 20px 0;
  text-align: left;
}

.header-page__logo {
  flex-shrink: 0;
  margin-right: 10px;
}

.header-page__nav {
  margin-left: auto;
}

.header-page__title {
  margin-bottom: 25px;
}

.logo {
  display: block;
}

.logo__pic {
  max-width: 100%;
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
}

.main-nav__item {
  padding-top: 5px;
}

.main-nav__item:last-child .main-nav__link {
  padding-right: 0;
}

.main-nav__link {
  display: block;
  padding: 5px 14px;
  font-family: bebas-font,sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #b9b9b9;
  text-decoration: none;
  transition: color 0.3s;
}

.main-nav__link.is-active {
  color: #e8603c;
}

.main-nav__link:hover {
  color: #e8603c;
}

.main__wrap {
  display: flex;
}

@media only screen and (max-width: 959px) {
  .main__wrap {
    flex-wrap: wrap;
    flex-wrap-flex-direction: column;
  }
}

.main__content {
  width: calc(100% - 230px);
  flex-grow: 1;
  padding-right: 40px;
}

@media only screen and (max-width: 959px) {
  .main__content {
    width: 100%;
    padding-right: 0;
  }
}

.main__posts {
  margin-bottom: 56px;
  padding-bottom: 44px;
}

.main__aside {
  width: 230px;
}

@media only screen and (max-width: 959px) {
  .main__aside {
    width: 100%;
    margin-top: 20px;
  }
}

.aside {
  padding-left: 11px;
  color: #464646;
}

@media only screen and (max-width: 959px) {
  .aside {
    padding-left: 0;
  }
}

.aside__title {
  margin-bottom: 19px;
}

.aside__sect {
  margin-bottom: 36px;
}

.aside__sect:last-child {
  margin-bottom: 0;
}

.title-sect {
  display: flex;
  padding-top: 2px;
  text-transform: uppercase;
  font-family: bebas-font,sans-serif;
  font-size: 14px;
  line-height: 1;
  font-weight: normal;
  color: #e8603c;
}

.title-sect:after {
  content: "";
  background: #ffffff;
  flex: 1;
  margin-left: 19px;
  background: url(../img/pattern.png) 0 0;
}

.slider {
  position: relative;
}

.slider__next, .slider__prev {
  position: absolute;
  top: 50%;
  left: 20px;
  z-index: 100;
  margin-top: -27px;
  font-size: 54px;
  cursor: pointer;
}

.slider__next {
  top: 46%;
  left: auto;
  right: 20px;
  transform: rotate(-180deg);
}

.slide {
  position: relative;
  height: 0;
  padding-bottom: 44.8%;
}

.slide__title {
  margin-bottom: 16px;
  font-family: bebas-font,sans-serif;
  font-size: 18px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
}

.slide p {
  font-size: 12px;
  line-height: 1.4;
  color: #ffffff;
}

.article {
  position: relative;
}

.article::before {
  content: "";
  position: absolute;
  right: 100%;
  top: 0;
  width: 75px;
  height: 75px;
  margin-right: 10px;
  background: #e8603c;
  background-image: url(../img/logo.png);
  background-position: center;
  background-repeat: no-repeat;
}

@media only screen and (max-width: 959px) {
  .article::before {
    position: relative;
    right: auto;
    display: block;
  }
}

.article::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 98.6%;
  height: 13px;
  background: url(../img/pattern.png) 0 0;
}

.article__media {
  margin-bottom: 17px;
}

.article__detail {
  display: flex;
}

.article__info {
  width: 22%;
  padding-right: 15px;
}

.article__descr {
  width: 78%;
}

.article__text p {
  margin-bottom: 17px;
}

.article__link {
  display: block;
  color: #414141;
  text-decoration: none;
}

.article__link:hover .article__title {
  opacity: 0.5;
}

.article__title {
  margin-top: 3px;
  margin-bottom: 19px;
  font-family: bebas-font,sans-serif;
  font-size: 18px;
  line-height: 1;
  font-weight: 500;
  text-transform: uppercase;
  color: #e8603c;
}

.article__text {
  font-size: 12px;
  line-height: 1.5;
  padding-right: 12px;
}

.article__info-title {
  margin-bottom: 4px;
  font-family: bebas-font,sans-serif;
  font-size: 12px;
  line-height: 1.66;
  color: #e8603c;
  text-transform: uppercase;
}

.article__info-descr {
  display: block;
  margin-bottom: 9px;
  font-size: 12px;
  line-height: 1.66;
  color: #bababa;
}

.article__info-tag {
  position: relative;
  padding-right: 7px;
  color: #bababa;
  text-decoration: none;
}

.article__info-tag:hover {
  text-decoration: underline;
}

.article__cite {
  display: block;
  margin-bottom: 6px;
  font-size: 12px;
  line-height: 1.5;
  color: #a0a0a0;
  text-decoration: none;
}

.article__cite:hover {
  text-decoration: underline;
}

.article--slider::before {
  background-image: url(../img/icon-photo.png);
}

.article--news::before {
  background-image: url(../img/icon-news.png);
}

.article--video::before {
  background-image: url(../img/icon-video.svg);
}

.article--quote::before {
  background-image: url(../img/icon-quote-white.svg);
  background-size: 34%;
}

.article--link::before {
  background-image: url(../img/icon-link.png);
}

.tag__link {
  display: inline-block;
}

.tag__link::before {
  content: "|";
  position: absolute;
  right: 0;
  bottom: 0;
}

.tag__link:last-child::before {
  display: none;
}

.quote {
  position: relative;
  padding: 21px 15px 20px 76px;
  font-size: 13px;
  line-height: 1.38;
  color: #474747;
  background-color: #f3f3f3;
  background-image: url(../img/pattern-quote.png);
}

.quote::before {
  content: "";
  position: absolute;
  left: 37px;
  top: 16px;
  width: 25px;
  height: 20px;
  background-image: url(../img/icon-quote.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.pagination {
  display: flex;
}

.pagination__item {
  height: 26px;
  width: 26px;
  margin-right: 4px;
  text-align: center;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
  background: #f3b29d;
}

.pagination__item.is-active {
  background: #e8603c;
}

.pagination__link {
  font-size: 14px;
  line-height: 26px;
  color: #ffffff;
  text-decoration: none;
}

.thumbs__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 7px;
}

@media only screen and (max-width: 620px) {
  .thumbs__list {
    grid-template-columns: repeat(2, 1fr);
  }
}

.thumbs__item {
  border: 1px solid #e8603c;
}

.thumbs__link {
  display: block;
  height: 0;
  padding-bottom: 100%;
}

.categories__link {
  color: #464646;
  text-decoration: none;
}

.categories__link:hover {
  text-decoration: underline;
}

.video-responsive {
  padding: 0;
  padding-bottom: 56.25%;
  position: relative;
  display: block;
  height: 0;
  overflow: hidden;
}

.video-responsive__iframe {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.footer-page {
  padding: 20px;
}
